import makerjs from 'makerjs';
import { EmptyModel } from './modelbasics.js';

export class ModelSplitter  {

  trimModelToLine(modelToSlice,lineAt,keepPositiveSide = true, alongXAxis = true, overlap = 5, includeCuttingLineHint = true, asClone = true) {
    //Clone or not the model
    if (asClone) {
      modelToSlice = makerjs.model.clone(modelToSlice);
    }

    let modelSize = makerjs.measure.modelExtents(modelToSlice);
    console.log('Cutting');

    //Create the cutter pieces
    let cutterPath, boundingBox;
    let boundingBoxWidth, boundingBoxOffsetX, boundingBoxHeight, boundingBoxOffsetY;

    if (alongXAxis) {
      cutterPath = new makerjs.paths.Line(
        [modelSize.low[0]+lineAt+(keepPositiveSide?-overlap:overlap),modelSize.low[1] - (modelSize.height*0.1)],
        [modelSize.low[0]+lineAt+(keepPositiveSide?-overlap:overlap),modelSize.high[1] + (modelSize.height*0.1)]
      );

      boundingBoxWidth = (keepPositiveSide?(modelSize.width-lineAt):(lineAt)) + (overlap*2);
      boundingBoxOffsetX = (keepPositiveSide?(modelSize.low[0]+lineAt-overlap):(modelSize.low[0]-overlap));
      boundingBoxHeight = modelSize.height + (overlap*2);
      boundingBoxOffsetY = modelSize.low[1] - overlap;
    } else {
      cutterPath = new makerjs.paths.Line(
        [modelSize.low[0] - (modelSize.height*0.1), modelSize.low[1]+lineAt+(keepPositiveSide?-overlap:overlap)],
        [modelSize.high[0] + (modelSize.height*0.1), modelSize.low[1]+lineAt+(keepPositiveSide?-overlap:overlap)]
      );

      boundingBoxWidth = modelSize.width + (overlap*2);
      boundingBoxOffsetX = modelSize.low[0] - overlap;
      boundingBoxHeight = (keepPositiveSide?(modelSize.height-lineAt):(lineAt)) + (overlap*2);
      boundingBoxOffsetY = (keepPositiveSide?(modelSize.low[0]+lineAt-overlap):(modelSize.low[0]-overlap));
    }
    boundingBox = makerjs.model.move(
      new makerjs.models.Rectangle(boundingBoxWidth,boundingBoxHeight),
      [boundingBoxOffsetX,boundingBoxOffsetY]
    );
    console.log(modelSize,boundingBoxOffsetX,boundingBoxWidth,boundingBoxOffsetY,boundingBox,boundingBoxHeight);

    //Jsut break the paths apart to start with
    makerjs.model.walk(modelToSlice,{
      onPath: (context) => {
        let pathIntersection = makerjs.path.intersection(context.pathContext,cutterPath);
        if (pathIntersection){
          pathIntersection.intersectionPoints.forEach(intersectionPoint => {
            let addPath = makerjs.path.breakAtPoint(context.pathContext,intersectionPoint);
            makerjs.model.addPath(context.modelContext,addPath);
          });
        }
      }
    });
    // //Now walk again trimming items that aren't in the bounding box
    makerjs.model.walk(modelToSlice,{
      onPath: (context) => {
        if (!makerjs.model.isPathInsideModel(context.pathContext,boundingBox,context.offset)) {
          delete context.modelContext.paths[context.pathId]
        }
      }
    });

    //Add the cut line hint line - ignoring overlap
    if (includeCuttingLineHint) {
      let cuttingLineHint;
      if (alongXAxis) {
        cuttingLineHint = new makerjs.paths.Line(
          [modelSize.low[0]+lineAt,modelSize.low[1] - (modelSize.height*0.1)],
          [modelSize.low[0]+lineAt,modelSize.high[1] + (modelSize.height*0.1)]
        );
      } else {
        cuttingLineHint = new makerjs.paths.Line(
          [modelSize.low[0] - (modelSize.width*0.1), modelSize.low[1]+lineAt],
          [modelSize.high[0] + (modelSize.width*0.1), modelSize.low[1]+lineAt]
        );
      }
      makerjs.path.layer(cuttingLineHint,'patternJoinLine');
      makerjs.model.addPath(modelToSlice,cuttingLineHint);
    }

    return modelToSlice
  }


  splitModel(modelToSplit,splitAtPct = .5,alongXAxis = true) {
    let modelExtents = makerjs.measure.modelExtents(modelToSplit);
    let splitAtDist = alongXAxis?(splitAtPct *  modelExtents.width):(splitAtPct *  modelExtents.height);
    console.log('splitting at',splitAtDist);
    let positiveSide = this.trimModelToLine(modelToSplit,splitAtDist,true,alongXAxis);
    let negativeSide = this.trimModelToLine(modelToSplit,splitAtDist,false,alongXAxis);
    return {
      models: {
        a: positiveSide,
        b: negativeSide
      }
    }
  }

}
